/* --- top-level container --- */
.lek-navbar {
  font-family: "Poppins", "Helvetica Neue", Arial, sans-serif;
  border-bottom: 1px solid #e9ecef;
  z-index: 1030;
}

/* logo */
.lek-logo img {
  height: 48px;
  width: auto;
  display: block;
}

/* center nav: ensure row direction and spacing */
.lek-nav-links {
  gap: 2rem;
  /* enforce horizontal layout even if other styles collide */
  display: flex !important;
  flex-direction: row !important;
  align-items: center;
  justify-content: center;
}

/* individual nav link */
.lek-nav-item {
  color: #333;
  text-decoration: none;
  font-weight: 500;
  font-size: 15px;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 0;
  white-space: nowrap;
  transition: color 0.18s ease, transform 0.12s ease;
}
.lek-nav-item:hover {
  color: #12c7be; /* eye-catching cyan */
  transform: translateY(-1px);
}
.lek-nav-item.active {
  color: #12c7be;
}

/* support block on right */
.lek-support {
  gap: 0.6rem;
}
.lek-support-icon {
  background: #e9fbfb;
  color: #12c7be;
  border-radius: 50%;
  padding: 7px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.lek-support-small {
  display: block;
  font-size: 12px;
  color: #6c757d;
  line-height: 1;
}
.lek-support-phone {
  font-size: 16px;
  color: #111;
  line-height: 1;
}

/* Responsive safety: make sure nav stays hidden on small screens */
@media (max-width: 767.98px) {
  .lek-nav-links,
  .lek-support {
    display: none !important;
  }
}

/* small polish */
.lek-navbar .container {
  max-width: 1180px;
}

/* if another global CSS makes anchors block, force inline behavior */
.lek-nav-links .lek-nav-item {
  display: inline-flex;
}
